.experience-item {
    display:flex;
    padding: 15px;
    margin-bottom: 15px;
    transition: opacity 0.3s ease;

    @media (max-width: 600px) {
      flex-direction: column; /* Change to column layout on smaller screens */
    }
}

.experience-date {
  flex-shrink: 0;
    padding-top: 3px;
    width: 160px;
    font-size: smaller;
}

.experience-company {
  color: #00C6BF;
}

.experience-details {
    flex-grow: 1;
}

.experience-details h3 {
    margin-top: 0px;
    margin-bottom: 5px;
}

.experience-details h4 {
  margin-top: 0px;
  margin-bottom: 5px;
}

.experience-details p {
    margin-top: 0px;
    margin-bottom: 5px;
}

.experience-tags {
    margin-top: 15px;
}

.tag {
    display: inline-block;
    background-color: rgba(0, 115, 131, 0.4);
    color: #538FF8;
    padding: 5px 10px;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 15px;
    font-size: 13px;
  }

.experience-item:hover {
  background-color: rgba(0, 66, 95, 0.4);
  /* cursor: pointer; */
  border-radius: 10px;
  opacity: 1;
  /* Add other hover effects as needed */
}

.hovered {
  opacity: 0.5;
}

.experience-link {
  display: block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
}

.experience-link:hover {
  text-decoration: underline;
}
  

